const maxDateTest = (value, context) => {
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  return today >= value
}

const maxTodayTest = (value, context) => {
  const today = new Date()
  today.setHours(23, 59, 59, 0)
  return today >= value
}
const maxBirthdayDay = (value) => differenceInYears(new Date(), new Date(value)) >= 18

