const minSevenDaysVacation = (endDate, context) => {
  const { start_date: startDate } = context?.parent
  return differenceInCalendarDays(new Date(endDate), new Date(startDate)) > 5
}

const endDateNotLessStartDate = (endDate, context) => {
  const { start_date: startDate } = context?.parent
  return endDate >= startDate
}

export const newVacationSchema = yup.object().shape({
  start_date: yup
    .date()
    .required(' ')
    .min(format(Date.now(), 'yyyy-MM-dd'), '    .   '),
  end_date: yup
    .date()
    .required(' ')
    .test('not less', '      ', endDateNotLessStartDate)
    .test('seven', ' .    ', minSevenDaysVacation),
})

