export const newResponseSchema = yup.object().shape({
  company_name: yup
    .string()
    .required('  ')
    .min(RESPONSE_TEXT_FIELDS_MIN_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .max(RESPONSE_TEXT_FIELD_MAX_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .matches(RESPONSE_WORK_REGEXP, RESPONSE_WORK_TIP),
  position: yup
    .string()
    .required(' ')
    .min(RESPONSE_TEXT_FIELDS_MIN_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .max(RESPONSE_TEXT_FIELD_MAX_SYMBOLS, RESPONSE_TEXT_FIELD_MINMAX_TIP)
    .matches(RESPONSE_WORK_REGEXP, RESPONSE_WORK_TIP),
  is_remote_work: yup.boolean(),
  cities: yup.array().nullable(true),
  comment: yup
    .string('   ')
    .max(RESPONSE_COMMENT_MAX_SYMBOLS, RESPONSE_COMMENT_MAX_SYMBOLS_TIP)
    .nullable(true),
  url: urlOption,
  salary: yup
    .number()
    .max(1000000000, '       1000000000')
    .nullable(true)
    .moreThan(-1, '     ')
    .typeError(' ')
    .transform((value, original) => (original == null || original === '' ? null : value)),
  salary_max: yup
    .number()
    .max(1000000000, '       1000000000')
    .nullable(true)
    .moreThan(-1, '     ')
    .typeError(' ')
    .transform((value, original) => (original == null || original === '' ? null : value))
    .when('salary', {
      is: (val) => val,
      then: (theme) => theme.min(yup.ref('salary'), '    ""'),
    }),
  color_id: yup.string().required(' '),
  response_date: yup
    .date()
    .required(' ')
    .test('max-current-date', '       ', maxDateTest)
    .test('min-two-weeks', '  ,     14  ', minResponseDate)
    .typeError(' '),
  hr_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
  test_task_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
  tech_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
  boss_interview_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
  offer_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
  rejection_date: yup
    .date()
    .nullable(true)
    .when(
      'response_date',
      (responseDate, schema) => responseDate && schema.min(responseDate, '     '),
    )
    .typeError(' '),
})

