export const vacancyFirstTabSchema = yup.object().shape({
  isSalaryNotRequired: yup.boolean(),
  isConfidentialLinkNotRequired: yup.boolean(),
  company_name: workVacancyOption,
  // courses: multipleValuesOptions,
  salary: yup
    .number()
    .max(1000000000, '       1000000000')
    .moreThan(-1, '     ')
    .transform((value, original) => (original == null || original === '' ? null : value))
    .nullable(true)
    .typeError('    0-9')
    .when('salary_max', {
      is: (val) => !val,
      then: (theme) => theme.required('     '),
      otherwise: (theme) => theme.max(yup.ref('salary_max'), '    ""'),
    })
    .when('isSalaryNotRequired', {
      is: true,
      then: (theme) => theme.notRequired(),
    }),
  salary_max: yup
    .number()
    .max(1000000000, '       1000000000')
    .moreThan(-1, '     ')
    .transform((value, original) => (original == null || original === '' ? null : value))
    .nullable(true)
    .typeError('    0-9'),
  website: urlOption,
  position: workVacancyOption,
  company_confidential_url: yup
    .string()
    .matches(URL_REG_EXP, '   ')
    .when('isConfidentialLinkNotRequired', {
      is: false,
      then: yup.string().required('    '),
    }),
  currency: yup.string().when('isSalaryNotRequired', {
    is: false,
    then: yup.string().required('    '),
  }),
})

